import React, { forwardRef } from 'react'
import { styled } from '@mui/system';
import { Box } from '@mui/material';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const GridBox = styled(Box)(({ theme }) => ({
  border: `1px solid ${theme.palette.grid.border}`,
  boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  borderRadius: '5px',
  width: '900px',
  margin: '0 auto',
}));

const GridBoxFull = styled(Box)(({ theme }) => ({
  border: `1px solid ${theme.palette.grid.border}`,
  boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  borderRadius: '5px',
  width: '1320px',
  margin: '0 auto',
}));

const GridBoxMedium = styled(Box)(({ theme }) => ({
  border: `1px solid ${theme.palette.grid.border}`,
  boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  borderRadius: '5px',
  width: '760px',
  margin: '0 auto',
}));

const GridBoxHome = styled(Box)(({ theme }) => ({
  width: '100%',
}));

const GridBoxExist = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  width: '100%',
  height: '420px',
  margin: '0 auto',
  overflowY: 'auto',
  backgroundColor: theme.palette.grid.exist.bg,
  borderTop: `1px solid ${theme.palette.grid.exist.border.wrap}`,
  borderBottom: `1px solid ${theme.palette.grid.exist.border.wrap}`,
}));

const GridBoxPayMent = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  width: '100%',
  height: '300px',
  margin: '0 auto',
  overflowY: 'auto',
  backgroundColor: '#fafafa',
  borderTop: `1px solid #AAAAAA`,
  borderBottom: `1px solid #AAAAAA`,
}));


const HeadBox = styled(Box)(({ theme }) => ({
  display: 'flex',
  gap: '5px',
  alignItems: 'center',
  padding: '15px 20px',
  height: '60px',
  fontSize: '18px',
  backgroundColor: theme.palette.background.default,
  borderBottom: `1px solid ${theme.palette.grid.border}`,
}));

const HeadBoxCol = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: '15px 22px 15px 0px',
  height: '60px',
  fontSize: '18px',
  backgroundColor: theme.palette.background.default,
  borderBottom: `1px solid ${theme.palette.grid.border}`,
}));

const RowWrapBox = styled(Box)(() => ({
  width: '100%',
  overflowY: 'scroll',
  height: '480px',
  paddingBottom: '10px',
  background: '#fff',
}));

const RowBox = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'row',
  alignItems: 'center',
  padding: '10px 0px',
  height: '60px',
  borderBottom: `1px solid ${theme.palette.grid.border}`,
  cursor: 'pointer',
  
  '&:hover': {
    backgroundColor: theme.palette.background.hover,
  },
}));

const RowBoxFix = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  height: '60px',
  fontSize: '18px',
  borderBottom: `1px solid ${theme.palette.grid.border}`,
  '&:hover': {
    backgroundColor: theme.palette.background.hover,
  },
}));

const RowBoxCalendar = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'row',
  alignItems: 'center',
  justifyContent: 'space-between',
  height: '60px',
  width: '930px',
  padding: '0',
}));

const ColBox = styled(Box)(({ theme }) => ({
  padding: '0 20px',
  fontSize: '15px',
  letterSpacing: 'normal',
}));

const ColBoxCalendar = styled(Box)(({ theme }) => ({
  width: '30px',
  textAlign:'center',
  height: '100%',
  marginLeft: '-1px',
  marginTop: '-1px',
  padding: '0',
}));

const ColChkBox = styled(Box)(({ theme }) => ({
  width: '45px',
  padding: '5px 8px 0 20px',
}));

// grid box return
const Grid = ({ full, modal, exist, payment, home, children, ...props }) => {
  if (modal) {
    return (
      <GridBoxMedium {...props}>
        { children }
      </GridBoxMedium>
    )
  }

  if (full) {
    return (
      <GridBoxFull {...props}>
        { children }
      </GridBoxFull>
    )
  }

  if (exist) {
    return (
      <GridBoxExist {...props}>
        { children }
      </GridBoxExist>
    )
  }

  if (home) {
    return (
      <GridBoxHome {...props}>
        { children }
      </GridBoxHome>
    )
  }

  if (payment) {
    return (
      <GridBoxPayMent {...props}>
        { children }
      </GridBoxPayMent>
    )
  }

  return (
    <GridBox {...props}>
      { children }
    </GridBox>
  )
}

// grid head return
const Head = ({ col, children, ...props }) => {
  if (col) {
    return(
      <HeadBoxCol {...props}>
        { children }
      </HeadBoxCol>
    )
  }

  return(
    <HeadBox {...props}>
      { children }
    </HeadBox>
  )
}

// grid row wrap return
const RowWrap = ({ children, ...props }) => {
  return(
    <RowWrapBox {...props}>
      { children }
    </RowWrapBox>
  )
}

// grid row return
const Row = forwardRef( ({ calendar, payment, children, ...props }, ref ) => {
  if (calendar) {
    return(
      <RowBoxCalendar ref={ref} {...props}>
        {children}
      </RowBoxCalendar>
    )
  }

  if (payment) {
    return (
      <RowBox style={{ backgroundColor: '#FFFFFF'}}>
        { children }
      </RowBox>
    )
  }

  return(
    <RowBox ref={ref} {...props}>
      { children }
    </RowBox>
  )
})

// grid col return
const Col = ({ w, fixed, calendar, children, ...props }) => {
  if (calendar) {
    return(
      <ColBoxCalendar className='border border-solid border-l-[1px] border-r-0 border-y-0 border-[#dddddd] flex justify-center items-center text-[16px] text-[#555555]' {...props}>
        { children }
      </ColBoxCalendar>
    )
  }

  if (fixed) {
    return (
      <ColBox style={{width: fixed}} {...props}>
        { children }
      </ColBox>
    )
  }

  return(
    <ColBox style={{flex:`${w}`}} {...props}>
      { children }
    </ColBox>
  )
}

const Chk = ({ children, ...props }) => {

  return(
    <ColChkBox {...props}>
      { children }
    </ColChkBox>
  )
}

const Arrow = () => {
  return(
    <div className="flex w-[35px] justify-end items-center pr-3">
      <ArrowForwardIosIcon fontSize='small' className='text-[#545454]' />
    </div>
  )
}

Grid.Head = Head;
Grid.RowWrap = RowWrap;
Grid.Row = Row;
Grid.Col = Col;
Grid.Chk = Chk;
Grid.Arrow = Arrow;

export { Grid };