import React, { useState } from 'react';
import { TwitterPicker } from 'react-color';
import { useDebug } from '../../../hooks/basic/useDebug';
import { rankColorList } from '../../../utils/rank/rankColorList';

const ColorListPicker = React.forwardRef(({ value, onChange, ...props }, ref) => {
  const [open, setOpen] = useState(false);

  const handleChange = (color, idx, name) => {
    onChange(color.hex, idx, name);
    setOpen(false);
  };

  // useDebug(color);
  return (
    <>
      <div
        className="ml-[6px] w-[25px] h-[25px] border border-solid border-[1px] border-[#bbbbbb] cursor-pointer rounded-[4px]"
        style={{ background: `${value || '#ffffff'}` }}
        onClick={() => setOpen((prev) => !prev)}
      ></div>
      {open && (
        <div className="absolute z-50">
          <TwitterPicker
            ref={ref}
            colors={rankColorList}
            color={value || '#ffffff'}
            onChange={handleChange}
            {...props}
          />
        </div>
      )}
    </>
  );
});

export default ColorListPicker;
