import uuid from 'react-uuid'

export function getFormData(dataList) {
  const formData = new FormData()
  
  for (let val of dataList) {
    if (val) {
      if (val[0] === 'data') {
        formData.append(
          val[0],
          `${val[1]}`,
        )
      } else {
        formData.append(
          val[0],
          val[1],
        )
      }
    }
  }

  return formData
}
