import React from 'react';
import InfoData from '../../../../components/UI/Atoms/Info/InfoData';
import CertificateProfile from '../../../../components/UI/Atoms/Profile/CertificateProfile';
import RankLevelupImage from '../../../../components/common/Rank/RankLevelupImage';
import moment from 'moment/moment';
import { getFullname } from '../../../../utils/render/getFullname';
import { useRecoilValue } from 'recoil';
import dojangInfoAtom from '../../../../recoil/atom/dojangInfoAtom';

const LevelInfoSample = ({ data }) => {
  const dojangInfo = useRecoilValue(dojangInfoAtom);

  return (
    <>
      <div className="h-full">
        <div id="levelup_rank" className="w-[70px] h-[50px] absolute top-[18rem] left-[20.2rem]">
          <RankLevelupImage src={data?.level_name} />
        </div>
        <div id="levelup_profile" className="absolute top-[180px] left-[100px]">
          <CertificateProfile width={99} height={127.5} shape={0} urlKey={data?.photo_url || ''} />
        </div>
        <div
          id="levelup_info"
          className="absolute top-[297px] left-[220px] flex flex-col justify-center gap-[16px] mt-[18px]"
        >
          <InfoData className="text-[14px] ml-[-25px]">KR</InfoData>
          <InfoData className="text-[14px] ml-[-55px]">대한민국</InfoData>
          <InfoData className="text-[14px] ml-[-55px]">강태권</InfoData>
          <InfoData className="text-[14px] ml-[-45px]">2023년 01월 01일</InfoData>
          <InfoData className="text-[14px] mt-[3px] ml-[-55px]">
            {dojangInfo.name || '○○태권도장'}
          </InfoData>
        </div>
        <div id="levelup_date" className="absolute bottom-[200px] left-[270px]">
          <InfoData className="text-[14px]">2023년 01월 01일</InfoData>
        </div>
      </div>
    </>
  );
};

export default LevelInfoSample;
