import * as React from 'react';
import { InputBase } from '@mui/material';

const Input = React.forwardRef(({ width, height, ...props }, ref) => {
  return (
    <InputBase
      ref={ref}
      sx={{
        width: width ? width : '80px',
        '& .MuiInputBase-input': {
          height: height ? height : '14px',
          fontSize: '1rem',
          fontWeight: 400,
          lineHeight: 1.5,
          borderRadius: '5px',
          border: '1px solid #BDBDBD',
          padding: '10px 12px',
          '&:focus': {
            borderColor: '#000000',
          },
        },
      }}
      {...props}
    ></InputBase>
  );
});

export default Input;
