import authApi from '../authApi'
import { FormDataConfig } from '../config'

export const getDojangInfo = async (dojangId) => {
  const res = await authApi.get(`dojang/dojangread/${dojangId}`)
  return res.data
}

export const createDojang = async (body) => {
  const res = await authApi.post(`dojang/dojangcreate`, body, FormDataConfig)
  return res
}

export const selectDojang = async () => {
  const res = await authApi.get(`dojang/dojangselect`)
  return res.data
}

export const updateDojangInfo = async (dojangId, body) => {
  const res = await authApi.post(`dojang/dojangupdate/${dojangId}`, body, FormDataConfig )
  return res
}

export const deleteDojang = async (dojangId) => {
  const res = await authApi.delete(`dojang/dojangdelete/${dojangId}`)
  return res.data
}

export const getDojangSchedule = async (dojangId) => {
  const res = await authApi.get(`dojang/dojang_home_schedule/${dojangId}`)
  return res.data
}

