export const getDojangLevelList = async (dojangId) => {
  const res = await authApi.get(`/dojang/levelinforead/${dojangId}`)
  return res.data
}

export const updateDojangLevelList = async (dojangId, body) => {
  const res = await authApi.post(`/dojang/levelinfoupdate/${dojangId}`, body, FormDataConfig)
  return res
}


export const getPidInfo = async (dojangId) => {
  const res = await authApi.get(`dojang/pid_read/${dojangId}`)
  return res.data
}


export const updatePidInfo = async (dojangId, body) => {
  const res = await authApi.post(`dojang/pid_create/${dojangId}`, body)
  return res.data
}