import React from 'react';
import StarIcon from '@mui/icons-material/Star';

const rankBox = {
  width: '30px',
  height: '18px',
  // border: '1px solid #000000',
  display: 'flex',
  flexDirection: 'column',
  // justifyContent: 'center',
  position: 'relative',
};

const RankOption = ({ value, ...props }) => {
  const style = {
    width: '100%',
    height: value?.belt_type == '3' ? '33.33333%' : value?.belt_type == '2' ? '50%' : '100%',
    outline: '1px solid #777777',
  };

  return (
    <div style={rankBox} {...props}>
      {value?.belt_type != '' && <div style={{ ...style, background: value?.first_line }}></div>}
      {(value?.belt_type == '2' || value?.belt_type == '3') && (
        <div
          style={{
            ...style,
            background: value?.second_line,
            marginTop: '1px',
            // height: '5px',
          }}
        >
          {value?.is_star && (
            <StarIcon className="relative top-[-9px] text-[10px] text-[#fe0] z-[100]" />
          )}
        </div>
      )}
      {value?.belt_type == '3' && (
        <div
          style={{
            ...style,
            background: value?.third_line,
            marginTop: '1px',
            // height: '5px',
          }}
        ></div>
      )}
    </div>
  );
};

export default RankOption;
